.flip-box-container{
    width: 100%;
    display: inline-block;
    padding: 15px;
    text-align: center;
}

.flip-box-inner {
    width: 100%;
    height: 350px;
    display: inline-block;
    margin-top: 10px;
    float: left;
    text-align: left;
    transition: transform 0.8s;
    transform-style: preserve-3d;
}

.flip-box:hover .flip-box-inner {
    transform: rotateY(180deg);
}

.flip-box-front, .flip-box-back {
    position: absolute;
    top: 0;
    width: 100%;
    height: 100%;
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;
    border-radius: 12px;
    box-shadow: 11px 11px 22px #14181b,
    -11px -11px 22px #3b4851;
}

.flip-box-front {
    background-color: #273036;
    color: #FF8A65;
    text-shadow: 0 5px 5px rgba(0, 0, 0, 1);
    font-weight: 500;
}

.flip-box-back {
    display: inline-block;
    background-color: white;
    color: #273036;
    transform: rotateY(180deg);
}

.flip-box-title {
    padding: 10px;
    display: inline-block;
    text-align: center;
    width: 100%;
}

.project-img {
    padding: 10px;
    display: inline-block;
    width: 100%;
    height: auto;
    overflow: auto;
    float: left;

}

.image {
    max-width: 100%;
