    background-color: transparent;
    text-shadow: 0 5px 5px rgba(0, 0, 0, 0.5);
    transition: transform .2s;
}
.github-icon {
    transition: transform .2s;
    border-radius: 12px;
    background: #273036;
    box-shadow:  5px 5px 11px #1d2429,
    -5px -5px 11px #313c44;
    padding: 5px;
}

.github-icon:hover {
    border-radius: 12px;
    background: #273036;
    box-shadow: inset 5px 5px 11px #1d2429,
    inset -5px -5px 11px #313c44;
}

