/* SVG Animations & CSS */

#react-logo, #background #Vector_3 {
    -webkit-animation: clockwiseRotate 5s linear infinite;
            animation: clockwiseRotate 5s linear infinite;
    transform-origin: center;
    transform-box: fill-box;
}

#background #Vector_5 {
    -webkit-animation: antiClockwiseRotate 5s linear infinite;
            animation: antiClockwiseRotate 5s linear infinite;
    transform-origin: center;
    transform-box: fill-box;
}

#man #Vector_151 {
    animation: manArms 0.5s ease-in-out infinite alternate-reverse;
    transform-origin: top;
}

#man #Vector_145 {
    -webkit-animation: manArms 1s ease-in-out infinite alternate;
            animation: manArms 1s ease-in-out infinite alternate;
    transform-origin: top;
}

#man #Vector_156 {
    animation: manBiceps 0.5s ease-in-out infinite alternate-reverse;
}

#java-logo #Vector_59, #java-logo #Vector_58 {
    -webkit-animation: java-swell 7s ease-in-out -1.25s infinite;
            animation: java-swell 7s ease-in-out -1.25s infinite;
    transform-origin: center;
    transform-box: fill-box;
}

#man #Vector_125, #Vector_143 {
    -webkit-animation: foot 1s ease-in-out infinite alternate;
            animation: foot 1s ease-in-out infinite alternate;
    transform-origin: top;
    transform-box: fill-box;

}

#Vector_142 {
    -webkit-animation: leg 1s ease-in-out infinite alternate;
            animation: leg 1s ease-in-out infinite alternate;
    transform-origin: top;
    transform-box: fill-box;
}

@-webkit-keyframes clockwiseRotate {
    from {
        transform: rotateZ(0deg);
    }
    to {
        transform: rotateZ(360deg);
    }
}

@keyframes clockwiseRotate {
    from {
        transform: rotateZ(0deg);
    }
    to {
        transform: rotateZ(360deg);
    }
}

@-webkit-keyframes antiClockwiseRotate {
    from {
        transform: rotateZ(0deg);
    }
    to {
        transform: rotateZ(-360deg);
    }
}

@keyframes antiClockwiseRotate {
    from {
        transform: rotateZ(0deg);
    }
    to {
        transform: rotateZ(-360deg);
    }
}

@-webkit-keyframes manArms {
    from{
        transform: rotateX(0deg);
        
    }
    to {
        transform: rotateX(7deg);
        
    }
}

@keyframes manArms {
    from{
        transform: rotateX(0deg);
        
    }
    to {
        transform: rotateX(7deg);
        
    }
}

