    from{
        transform: rotateX(0deg);
    }
    to {
        transform: rotateX(2deg);
    }
}

@keyframes manBiceps {
    from{
        transform: rotateX(0deg);
    }
    to {
        transform: rotateX(2deg);
    }
}

@-webkit-keyframes java-swell {
    0%, 100% {
    transform: translate3d(0,-5px,0);
    }
    50% {
    transform: translate3d(0,5px,0);
    }
}

@keyframes java-swell {
    0%, 100% {
    transform: translate3d(0,-5px,0);
    }
    50% {
    transform: translate3d(0,5px,0);
    }
}

@-webkit-keyframes foot {
